import React, { useState } from "react";
import Avatar from "@material-ui/core/Avatar";
import Button from "@material-ui/core/Button";
import CssBaseline from "@material-ui/core/CssBaseline";
import TextField from "@material-ui/core/TextField";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
import Grid from "@material-ui/core/Grid";
import Box from "@material-ui/core/Box";
import LockOutlinedIcon from "@material-ui/icons/LockOutlined";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import Container from "@material-ui/core/Container";
import SignIn from "../signin/signIn";
import { Link } from "react-router-dom";
import { SuccessMessages, UserRole } from "../../assets/constants/constants";
import {
  CircularProgress,
  FormControl,
  InputLabel,
  MenuItem,
  Select,
} from "@material-ui/core";
import { usersAtom } from "../../atoms/atom";
import { useAtom } from "jotai";
import UserValidator from "../../libs/validators/user.validator";
import { userSignUpSchema } from "../../joi-schema/user-joi-schema";
import UserService from "../../services/user.service";
import { toast } from "react-toastify";

const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(8),
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: "100%", // Fix IE 11 issue.
    marginTop: theme.spacing(3),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));

export default function SignUp(props) {
  const classes = useStyles();
  const [error, setError] = useState("");
  const [name, setname] = useState();
  const [email, setemail] = useState();
  const [password, setpassword] = useState();
  const [role, setrole] = useState();
  const [open, setOpen] = React.useState(false);
  const [users, setUsers] = useAtom(usersAtom);
  const [loading, setLoading] = useState(false);
  const createUser = async (e) => {
    e.preventDefault();

    const data = {
      username: name,
      email,
      password,
      role,
    };
    const { error, value } = UserValidator.validateUserCreateSchema(
      data,
      userSignUpSchema
    );
    console.log(error, value);
    if (error) {
      setError(error.details[0].message);
    } else {
      setError("");
      setLoading(true);
      const res = await UserService.createUser(data);
      console.log(res);
      setLoading(false);
      if (!res.success) {
        toast.error(res.message, {
          position: "top-right",
          autoClose: 3000,
          closeOnClick: true,
        });
        return;
      }

      setUsers([...users, res.data.newUser]);
      toast.success(SuccessMessages.userCreatedSuccess, {
        position: "top-right",
        autoClose: 3000,
        closeOnClick: true,
      });
    }
  };

  return (
    <Container component="main" maxWidth="xs">
      <CssBaseline />
      <div className={classes.paper}>
        <Avatar className={classes.avatar}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography component="h1" variant="h5">
          Sign up
        </Typography>
        <form className={classes.form} noValidate>
          <Grid container spacing={2}>
            <Grid item xs={12}>
              <TextField
                variant="outlined"
                required
                fullWidth
                label="Name"
                autoFocus
                onChange={(event) => setname(event.target.value)}
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                variant="outlined"
                required
                fullWidth
                label="Email"
                autoFocus
                onChange={(event) => setemail(event.target.value)}
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                type="password"
                variant="outlined"
                required
                fullWidth
                label="Password"
                autoFocus
                onChange={(event) => setpassword(event.target.value)}
              />
            </Grid>

            <Grid item xs={12}>
              <FormControl className={classes.formControl}>
                <InputLabel id="demo-simple-select-label">Role</InputLabel>
                <Select
                  labelId="demo-simple-select-label"
                  id="demo-simple-select"
                  value="Role"
                  onChange={(e) => setrole(e.target.value)}
                >
                  <MenuItem value={UserRole.CLIENT}>{UserRole.CLIENT}</MenuItem>
                  <MenuItem value={UserRole.REALTOR}>
                    {UserRole.REALTOR}
                  </MenuItem>
                </Select>
              </FormControl>
              <Box color="error.main">{error}</Box>
              <div style={{ display: "flex", justifyContent: "center" }}>
                {loading ? <CircularProgress /> : ""}
              </div>
            </Grid>
          </Grid>
          <Button
            type="submit"
            fullWidth
            variant="contained"
            color="primary"
            className={classes.submit}
            onClick={(event) => createUser(event)}
          >
            SignUp
          </Button>
          <Grid container justify="flex-end">
            <Grid item>
              <Link to="/signin">Already have an account? Sign in</Link>
            </Grid>
          </Grid>
        </form>
      </div>
      <Box mt={5}></Box>
    </Container>
  );
}
