import React from "react";
import { withStyles, makeStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";
import { userStyles } from "../styles/styles";
import UserService from "../../services/user.service";
import { userAtom, usersAtom } from "../../atoms/atom";
import { useAtom } from "jotai";
import { useState, useEffect } from "react";
import DeleteIcon from "@material-ui/icons/Delete";
import UpdateIcon from "@material-ui/icons/Update";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { SuccessMessages, UserAction } from "../../assets/constants/constants";
import UserModal from "../userModal/userModal";
import AppBar from "@material-ui/core/AppBar";
import Typography from "@material-ui/core/Typography";
import Toolbar from "@material-ui/core/Toolbar";
import Logout from "../logout/logout";
import HomeWorkIcon from "@material-ui/icons/HomeWork";
import Box from "@material-ui/core/Box";
import AuthUtil from "../../util/auth.util";
import { Link } from "react-router-dom";
import { Pagination } from "@material-ui/lab";
import { Button, CircularProgress, Grid } from "@material-ui/core";

const StyledTableCell = withStyles((theme) => ({
  head: {
    backgroundColor: theme.palette.common.black,
    color: theme.palette.common.white,
  },
  body: {
    fontSize: 14,
  },
}))(TableCell);

const StyledTableRow = withStyles((theme) => ({
  root: {
    "&:nth-of-type(odd)": {
      backgroundColor: theme.palette.action.hover,
    },
  },
}))(TableRow);

const useStyles = makeStyles({
  table: {
    width: "85%",
    margin: "auto",
    marginTop: 20,
    marginBottom: 50,
  },
  appbar: {
    marginBottom: "250px",
    color: "black",
  },
  icon: {
    marginRight: "10px",
    cursor: "pointer",
  },
  title: {
    display: "flex",
    flexGrow: 1,
    justifyContent: "center",
  },
  footer: {
    padding: "20px",
  },
});

export default function Users() {
  const classes = useStyles();
  const [user, setUser] = useAtom(userAtom);
  const [users, setUsers] = useAtom(usersAtom);

  const [dense, setDense] = React.useState(false);
  const [secondary, setSecondary] = React.useState(false);
  const [loading, setloading] = useState(false);
  const [page, setPage] = useState(1);

  useEffect(async () => {
    const currentUser = await UserService.getCurrentUser();

    setUser(currentUser.data.authUser);

    const data = await getUsers(page);

    setUsers(data);
  }, []);

  const getUsers = async (page) => {
    if (AuthUtil.getJWTToken()) {
      setloading(true);
      const res = await UserService.getUsers(page);

      setloading(false);
      if (!res.success) {
        toast.error(res.message, {
          position: "top-right",
          autoClose: 3000,
          closeOnClick: true,
        });
        return [];
      }
      return res.data;
    }
    return [];
  };

  const updateUser = async () => {
    const res = await UserService.updateUser(user);

    if (!res.success) {
      toast.error(res.message, {
        position: "top-right",
        autoClose: 3000,
        closeOnClick: true,
      });
      return;
    }
    setUsers(users.filter((u) => u.uuid !== user.uuid));

    toast.success(SuccessMessages.userDeltedSuccess, {
      position: "top-right",
      autoClose: 3000,
      closeOnClick: true,
    });
  };

  const deleteUser = async (user) => {
    const res = await UserService.deleteUser(user);

    if (!res.success) {
      toast.error(res.message, {
        position: "top-right",
        autoClose: 3000,
        closeOnClick: true,
      });
      return;
    }
    setUsers(users.filter((u) => u.uuid !== user.uuid));

    toast.success(SuccessMessages.userDeltedSuccess, {
      position: "top-right",
      autoClose: 3000,
      closeOnClick: true,
    });
  };

  return (
    <div>
      <AppBar position="relative">
        <Toolbar>
          <Link to="/" style={{ color: "white" }}>
            <HomeWorkIcon className={classes.icon} fontSize="large" />
          </Link>
          <Typography
            variant="h6"
            color="inherit"
            className={classes.title}
            noWrap
          >
            Appartment Finder
          </Typography>

          <Logout name={user.username} />
        </Toolbar>
      </AppBar>

      <Box display="flex" p={1}>
        <Box p={1} flexGrow={1}></Box>
        <Box m={4}>
          <UserModal p={1} action={UserAction.CreateUser} />
        </Box>
      </Box>

      <TableContainer component={Paper}>
        <Table className={classes.table} aria-label="customized table">
          <TableHead>
            <TableRow>
              <StyledTableCell align="center">S.No</StyledTableCell>
              <StyledTableCell align="center">Name</StyledTableCell>
              <StyledTableCell align="center">Email</StyledTableCell>
              <StyledTableCell align="center">Role</StyledTableCell>

              <StyledTableCell align="center">Update</StyledTableCell>
              <StyledTableCell align="center">Delete</StyledTableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {users.map((user, index) => (
              <StyledTableRow key={user.uuid}>
                <StyledTableCell align="center">{index + 1}</StyledTableCell>
                <StyledTableCell align="center" component="th" scope="row">
                  {user.username}
                </StyledTableCell>
                <StyledTableCell align="center">{user.email}</StyledTableCell>
                <StyledTableCell align="center">{user.role}</StyledTableCell>
                <StyledTableCell align="center">
                  <UserModal
                    action={UserAction.UpdateUser}
                    userId={user.uuid}
                  />
                </StyledTableCell>

                <StyledTableCell align="center">
                  <DeleteIcon
                    className={classes.icon}
                    fontSize="large"
                    color="secondary"
                    cursor="pointer"
                    onClick={(e) => deleteUser(user)}
                  />
                </StyledTableCell>
              </StyledTableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
      <div style={{ display: "flex", justifyContent: "center" }}>
        {loading ? <CircularProgress /> : ""}
      </div>
      <footer className={classes.footer}>
        <Grid container direction="row" justify="center" alignItems="center">
          <Button
            variant="contained"
            color="primary"
            onClick={async () => {
              await setPage(page + 1);
              const newUsers = await getUsers(page + 1);
              setUsers([...users, ...newUsers]);
            }}
          >
            Load More...
          </Button>
        </Grid>
      </footer>
    </div>
  );
}
