import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Modal from "@material-ui/core/Modal";
import Backdrop from "@material-ui/core/Backdrop";
import Fade from "@material-ui/core/Fade";
import AddIcon from "@material-ui/icons/Add";
import Button from "@material-ui/core/Button";
import { RadioGroup, Radio, CircularProgress } from "@material-ui/core";
import CssBaseline from "@material-ui/core/CssBaseline";
import TextField from "@material-ui/core/TextField";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
import Link from "@material-ui/core/Link";
import Grid from "@material-ui/core/Grid";
import Box from "@material-ui/core/Box";
import LockOutlinedIcon from "@material-ui/icons/LockOutlined";
import Typography from "@material-ui/core/Typography";
import UpdateIcon from "@material-ui/icons/Update";
import TextareaAutosize from "@material-ui/core/TextareaAutosize";
import Container from "@material-ui/core/Container";
import { useEffect, useState } from "react";
import InputLabel from "@material-ui/core/InputLabel";
import MenuItem from "@material-ui/core/MenuItem";
import FormHelperText from "@material-ui/core/FormHelperText";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";
import {
  filterApartmentsOptions,
  SuccessMessages,
  ApartmentAction,
  UserAction,
  UserRole,
} from "../../assets/constants/constants";
import { apartmentModalStyles } from "../styles/styles";
import ApartmentService from "../../services/apartment.service";
import ApartmentValidator from "../../libs/validators/apartment.validator";
import { ToastContainer, toast } from "react-toastify";
import { useAtom, atom } from "jotai";
import { apartmentAtom, usersAtom } from "../../atoms/atom";
import UserValidator from "../../libs/validators/user.validator";
import UserService from "../../services/user.service";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import { userSignUpSchema } from "../../joi-schema/user-joi-schema";
export default function UserModal(props) {
  const classes = apartmentModalStyles();

  const [error, setError] = useState("");
  const [name, setname] = useState();
  const [email, setemail] = useState();
  const [password, setpassword] = useState();
  const [role, setrole] = useState();
  const [open, setOpen] = React.useState(false);
  const [users, setUsers] = useAtom(usersAtom);
  const [loading, setloading] = useState(false);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
    setError("");
  };

  const createUser = async (e) => {
    e.preventDefault();

    const data = {
      username: name,
      email,
      password,
      role,
    };
    const { error, value } = UserValidator.validateUserCreateSchema(
      data,
      userSignUpSchema
    );

    if (error) {
      setError(error.details[0].message);
    } else {
      setError("");
      setloading(true);
      const res = await UserService.createUser(data);

      setloading(false);
      if (!res.success) {
        toast.error(res.message, {
          position: "top-right",
          autoClose: 3000,
          closeOnClick: true,
        });
        return;
      }

      setUsers([res.data.newUser, ...users]);
      toast.success(SuccessMessages.userCreatedSuccess, {
        position: "top-right",
        autoClose: 3000,
        closeOnClick: true,
      });
      handleClose();
    }
  };

  const updateUser = async (e, userId) => {
    e.preventDefault();

    const data = {
      username: name,
      email,
      password,
      role,
    };
    const { error, value } = UserValidator.validateUserUpdateSchema(data);

    if (error) {
      setError(error.details[0].message);
    } else {
      setError("");
      setloading(true);
      const res = await UserService.updateUser(data, userId);

      setloading(false);
      if (!res.success) {
        toast.error(res.message, {
          position: "top-right",
          autoClose: 3000,
          closeOnClick: true,
        });
        return;
      }
      const filteredUsers = users.map((user) =>
        user.uuid === userId ? res.data : user
      );
      setUsers(filteredUsers);
      toast.success(SuccessMessages.userUpdateSuccess, {
        position: "top-right",
        autoClose: 3000,
        closeOnClick: true,
      });
      handleClose();
    }
  };
  return (
    <div>
      {props.action == UserAction.CreateUser ? (
        <Button variant="contained" color="secondary" onClick={handleOpen}>
          <AddIcon className={classes.icon} fontSize="large" />
          Create New User
        </Button>
      ) : (
        <UpdateIcon
          className={classes.icon}
          fontSize="large"
          color="primary"
          cursor="pointer"
          onClick={handleOpen}
        />
      )}
      <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        className={classes.modal}
        open={open}
        onClose={handleClose}
        closeAfterTransition
        BackdropProps={{
          timeout: 500,
        }}
      >
        <Fade in={open}>
          <div className={classes.paper}>
            <Container component="main" maxWidth="xs">
              <CssBaseline />
              <div className={classes.paper}>
                <Typography component="h1" variant="h5">
                  {props.action === UserAction.CreateUser
                    ? "Create New User"
                    : "Update Existing user"}
                </Typography>
                <form className={classes.form} noValidate>
                  <Grid container spacing={2}>
                    <Grid item xs={12}>
                      <TextField
                        variant="outlined"
                        required
                        fullWidth
                        label="Name"
                        autoFocus
                        onChange={(event) => setname(event.target.value)}
                      />
                    </Grid>
                    <Grid item xs={12}>
                      <TextField
                        variant="outlined"
                        required
                        fullWidth
                        label="Email"
                        autoFocus
                        onChange={(event) => setemail(event.target.value)}
                      />
                    </Grid>
                    <Grid item xs={12}>
                      <TextField
                        type="password"
                        variant="outlined"
                        required
                        fullWidth
                        label="Password"
                        autoFocus
                        onChange={(event) => setpassword(event.target.value)}
                      />
                    </Grid>

                    <Grid item xs={12}>
                      <FormControl className={classes.formControl}>
                        <InputLabel id="demo-simple-select-label">
                          Role
                        </InputLabel>
                        <Select
                          labelId="demo-simple-select-label"
                          id="demo-simple-select"
                          value={role}
                          style={{ width: "140px" }}
                          onChange={(e) => setrole(e.target.value)}
                        >
                          <MenuItem value={UserRole.CLIENT}>
                            {UserRole.CLIENT}
                          </MenuItem>
                          <MenuItem value={UserRole.REALTOR}>
                            {UserRole.REALTOR}
                          </MenuItem>
                          <MenuItem value={UserRole.ADMIN}>
                            {UserRole.ADMIN}
                          </MenuItem>
                        </Select>
                      </FormControl>
                    </Grid>
                  </Grid>
                  <Box color="error.main">{error}</Box>
                  <div style={{ display: "flex", justifyContent: "center" }}>
                    {loading ? <CircularProgress /> : ""}
                  </div>
                  <Button
                    type="submit"
                    fullWidth
                    variant="contained"
                    color="primary"
                    className={classes.submit}
                    onClick={(event) =>
                      props.action === UserAction.CreateUser
                        ? createUser(event)
                        : updateUser(event, props.userId)
                    }
                  >
                    {props.action === UserAction.CreateUser
                      ? "Create New User"
                      : "Update Existing user"}
                  </Button>
                </form>
              </div>
              <Box mt={5}></Box>
            </Container>
          </div>
        </Fade>
      </Modal>
    </div>
  );
}
