import * as Joi from "joi-browser";
import {
  userLoginSchema,
  userSignUpSchema,
  userUpdateJoiSchema,
} from "../../joi-schema/user-joi-schema";
class UserValidator {
  static validateUsername(data) {
    const res = Joi.validate(data, userLoginSchema);
    return res;
  }

  static validateUserUpdateSchema(data) {
    const res = Joi.validate(data, userUpdateJoiSchema);
    return res;
  }

  static validateUserCreateSchema(data) {
    const res = Joi.validate(data, userSignUpSchema);
    return res;
  }
}

export default UserValidator;
