import APIServices from "./api.services";
import ServiceUrls from "./service.url";
import AuthUtil from "../util/auth.util";
class ApartmentService {
  static async getApartments(data) {
    return APIServices.get({
      url: ServiceUrls.getApartmentsUrl,
      queryParams: data,
      headers: AuthUtil.getHeaders(),
    });
  }
  static async deleteApartment(apartmentId) {
    return APIServices.delete({
      url: `${ServiceUrls.deleteApartmentUrl}${apartmentId}`,
      headers: AuthUtil.getHeaders(),
    });
  }

  static async addApartment(body) {
    return APIServices.post({
      url: ServiceUrls.addApartmentUrl,
      headers: AuthUtil.getHeaders(),
      data: body,
    });
  }

  static async updateApartment(apartmentId, body) {
    return APIServices.put({
      url: `${ServiceUrls.updateApartmentUrl}${apartmentId}`,
      headers: AuthUtil.getHeaders(),
      data: body,
    });
  }
}

export default ApartmentService;
