import React from "react";
import Logout from "../logout/logout";
import AppBar from "@material-ui/core/AppBar";
import Button from "@material-ui/core/Button";
import CameraIcon from "@material-ui/icons/PhotoCamera";
import Card from "@material-ui/core/Card";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
import CssBaseline from "@material-ui/core/CssBaseline";
import Grid from "@material-ui/core/Grid";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import Container from "@material-ui/core/Container";

import HomeWorkIcon from "@material-ui/icons/HomeWork";
import RoomIcon from "@material-ui/icons/Room";
import ListIcon from "@material-ui/icons/List";
import { homeStyles } from "../styles/styles";
import { useEffect, useState } from "react";
import ApartmentService from "../../services/apartment.service";
import { useAtom, atom } from "jotai";
import { userAtom, modalAtom } from "../../atoms/atom";
import { CircularProgress, TextField } from "@material-ui/core";
import Box from "@material-ui/core/Box";
import {
  filterApartmentsOptions,
  SuccessMessages,
  ApartmentAction,
  View,
  LoadType,
} from "../../assets/constants/constants";
import UserService from "../../services/user.service";
import PermissionService from "../../services/permission.service";
import DeleteIcon from "@material-ui/icons/Delete";
import UpdateIcon from "@material-ui/icons/Update";
import AddIcon from "@material-ui/icons/Add";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import ApartmentModal from "../createApartment/apartmentModal";
import { apartmentAtom } from "../../atoms/atom";
import Chip from "@material-ui/core/Chip";
import GroupIcon from "@material-ui/icons/Group";
import { Link } from "react-router-dom";
import GoogleMap from "../google-map/googlemap";
import { Pagination } from "@material-ui/lab";
import AuthUtil from "../../util/auth.util";
import ApartmentValidator from "../../libs/validators/apartment.validator";
export default function Home() {
  const classes = homeStyles();

  const [page, setPage] = useState(1);
  const [user, setUser] = useAtom(userAtom);
  const [apartments, setApartments] = useAtom(apartmentAtom);

  const [view, setview] = useState(View.ListView);
  const [loading, setloading] = useState(false);

  const [minsize, setMinsize] = useState("");
  const [maxsize, setMaxsize] = useState("");
  const [minprice, setMinprice] = useState("");
  const [maxprice, setMaxprice] = useState("");
  const [minrooms, setMinrooms] = useState("");
  const [maxrooms, setMaxrooms] = useState("");

  useEffect(async () => {
    if (AuthUtil.getJWTToken()) {
      const currentUser = await getCurrentUser();

      setUser(currentUser);

      if (currentUser.username) {
        getApartments(page, LoadType.LoadFirst);
      }
    } else {
      setUser({});
    }
    console.log(apartments);
  }, []);

  const getCurrentUser = async () => {
    const res = await UserService.getCurrentUser();

    if (!res.success) {
      toast.error(res.message, {
        position: "top-right",
        autoClose: 3000,
        closeOnClick: true,
      });
      return res.data;
    }
    return res.data.authUser;
  };
  const getApartments = async (page, loadType) => {
    let data = Object.assign(
      {},
      minsize == "" ? null : { min_size: minsize },
      maxsize == "" ? null : { max_size: maxsize },
      minprice == "" ? null : { min_price: minprice },
      maxprice == "" ? null : { max_price: maxprice },
      minrooms == "" ? null : { min_rooms: minrooms },
      maxrooms == "" ? null : { max_rooms: maxrooms }
    );

    const { error, value } = ApartmentValidator.validateFilterApartment(data);

    if (error) {
      toast.error(error.details[0].message, {
        position: "top-right",
        autoClose: 3000,
        closeOnClick: true,
      });
    } else {
      setloading(true);

      const res = await ApartmentService.getApartments({ ...data, page });
      setloading(false);

      if (!res.success) {
        toast.error(res.message, {
          position: "top-right",
          autoClose: 3000,
          closeOnClick: true,
        });
        return;
      }

      if (loadType == LoadType.LoadFirst) setApartments(res.data);
      else setApartments([...apartments, ...res.data]);
    }
  };

  const deleteApartment = async (apartment) => {
    const res = await ApartmentService.deleteApartment(apartment.uuid);
    if (!res.success) {
      //set toasts msg "unable to find apartmentss"
      return;
    }
    toast.success(SuccessMessages.ApartmentDeletedSuccess, {
      position: "top-right",
      autoClose: 3000,
      closeOnClick: true,
    });
    setApartments(apartments.filter((ap) => ap.uuid !== apartment.uuid));
  };

  const setFilterOptions = (e, options) => {
    let value = e.target.value;

    switch (options) {
      case filterApartmentsOptions[0]:
        setMinsize(value);
        break;
      case filterApartmentsOptions[1]:
        setMaxsize(value);
        break;
      case filterApartmentsOptions[2]:
        setMinrooms(value);
        break;
      case filterApartmentsOptions[3]:
        setMaxrooms(value);
        break;
      case filterApartmentsOptions[4]:
        setMinprice(value);
        break;
      case filterApartmentsOptions[5]:
        setMaxprice(value);
        break;
    }
  };

  const searchFilterApartments = (e) => {
    e.preventDefault();
    setPage(1);
    getApartments(1, LoadType.LoadFirst);
  };

  return (
    <React.Fragment>
      <CssBaseline />
      <AppBar position="relative">
        <Toolbar>
          <Link to="/" style={{ color: "white" }}>
            <HomeWorkIcon className={classes.icon} fontSize="large" />
          </Link>
          <Typography
            variant="h6"
            color="inherit"
            className={classes.title}
            noWrap
          >
            Appartment Finder
          </Typography>

          <Logout name={user.username} />
        </Toolbar>
      </AppBar>

      <main>
        <div className={classes.heroContent}>
          {PermissionService.canAddApartment(user) ? (
            <Box m={3} mt={0} mb={5}>
              <Grid
                container
                spacing={3}
                direction="row"
                justify="flex-end"
                alignItems="center"
                spacing="4"
                xs={9}
                sm={12}
                md={12}
                lg={12}
              >
                {PermissionService.canGetUsers(user) ? (
                  <Grid item>
                    <Link to="/users" variant="body2" className={classes.link}>
                      <Button variant="contained" color="secondary">
                        <GroupIcon className={classes.icon} fontSize="large" />
                        Manage Users
                      </Button>
                    </Link>
                  </Grid>
                ) : (
                  ""
                )}

                <ApartmentModal action={ApartmentAction.CreateApartment} />
              </Grid>
            </Box>
          ) : (
            ""
          )}

          <Container maxWidth="sm">
            <Typography
              component="h1"
              variant="h2"
              align="center"
              color="textPrimary"
              gutterBottom
            >
              Apartment Finder
            </Typography>
            <Typography
              variant="h5"
              align="center"
              color="textSecondary"
              paragraph
            >
              Luxury appartment for rent nearby at reasonable price..
            </Typography>
            <div className={classes.heroButtons}>
              <Grid container spacing={2} justify="center">
                <Grid item>
                  <Button
                    variant="contained"
                    color="primary"
                    onClick={() => setview(View.ListView)}
                  >
                    <ListIcon className={classes.icon} />
                    Apartment on List
                  </Button>
                </Grid>
                <Grid item>
                  <Button
                    gutterBottom
                    variant="outlined"
                    color="primary"
                    onClick={() => setview(View.MapView)}
                  >
                    <RoomIcon className={classes.icon} />
                    Apartment on Map
                  </Button>
                </Grid>
              </Grid>
            </div>
          </Container>
          <Container>
            <Box className={classes.filterOptions}>
              {filterApartmentsOptions.map((options, key) => (
                <TextField
                  id="filled-number"
                  label={options}
                  type="number"
                  className={classes.filter}
                  InputProps={{ inputProps: { min: 0 } }}
                  onChange={(e) => setFilterOptions(e, options)}
                />
              ))}
            </Box>
            <Button
              variant="contained"
              color="secondary"
              onClick={(e) => searchFilterApartments(e)}
            >
              Search
            </Button>
          </Container>
        </div>
        <div style={{ display: "flex", justifyContent: "center" }}>
          {loading ? <CircularProgress /> : ""}
        </div>
        <Container className={classes.cardGrid} maxWidth="md">
          {view === View.ListView ? (
            <Grid container spacing={8}>
              {apartments.map((apartment) => (
                <Grid item key={apartment.uuid} xs={12} sm={6} md={4} lg={6}>
                  <Card className={classes.card}>
                    <CardMedia
                      className={classes.cardMedia}
                      image="https://www.investopedia.com/thmb/lUMUQyheogwYhtdQ_p8aBDNG9fg=/680x440/filters:fill(auto,1)/rent_house_73089751-5bfc333346e0fb002602ddbe.jpg"
                      title="Image title"
                    />
                    <CardContent className={classes.cardContent}>
                      <Typography gutterBottom variant="h5" component="h2">
                        {apartment.name}
                      </Typography>
                      <Typography variant="body1" component="h2">
                        {apartment.description}
                      </Typography>
                      <Typography variant="h6" component="h2">
                        Area: {apartment.floorSize}sq.ft
                      </Typography>
                      <Typography variant="h6" component="h2">
                        Rooms: {apartment.rooms} rooms
                      </Typography>

                      <Typography variant="h6" component="h2">
                        Price: {apartment.price}/-
                      </Typography>

                      <Chip
                        label={apartment.available ? "For Sale" : "Sold"}
                        color={apartment.available ? "primary" : "secondary"}
                      />
                    </CardContent>
                    {PermissionService.canUpdateOrDeleteApartment(
                      user,
                      apartment
                    ) ? (
                      <CardActions>
                        <ApartmentModal
                          action={ApartmentAction.UpdateApartment}
                          apartmentId={apartment.uuid}
                          apartment={apartment}
                        />

                        <DeleteIcon
                          className={classes.icon}
                          fontSize="large"
                          color="secondary"
                          onClick={(e) => deleteApartment(apartment)}
                        />
                      </CardActions>
                    ) : (
                      " "
                    )}
                  </Card>
                </Grid>
              ))}
            </Grid>
          ) : (
            <GoogleMap apartments={apartments} />
          )}
        </Container>
      </main>
      {/* Footer */}

      {view === View.ListView ? (
        <footer className={classes.footer}>
          <Grid container direction="row" justify="center" alignItems="center">
            <Button
              variant="contained"
              color="primary"
              onClick={async () => {
                await setPage(page + 1);
                getApartments(page + 1, LoadType.LoadMore);
              }}
            >
              Load More...
            </Button>
          </Grid>
        </footer>
      ) : (
        ""
      )}

      {/* End footer */}
    </React.Fragment>
  );
}
