import { UserRole } from "../../src/assets/constants/constants";
export default class PermissionService {
  static allowAdmin(authUser) {
    if (authUser.role === UserRole.ADMIN) return true;
    return false;
  }

  static allowAdminAndRealtor(authUser) {
    if (authUser === UserRole.ADMIN || authUser === UserRole.REALTOR)
      return true;
    return false;
  }

  static canRealtorUpdatOrDelete(authUser, apartment) {
    if (authUser.uuid == apartment.userId) {
      return true;
    }
    return false;
  }
  static canGetUsers(auth) {
    return this.allowAdmin(auth);
  }

  static canGetUser(auth) {
    return this.allowAdmin(auth);
  }

  static canDeleteUser(auth) {
    return this.allowAdmin(auth);
  }

  static canUpdatUser(auth) {
    return this.allowAdmin(auth);
  }

  static canAddApartment(authUser) {
    if (authUser.role === UserRole.ADMIN || authUser.role === UserRole.REALTOR)
      return true;
    return false;
  }
  static canUpdateApartment(auth) {
    return this.allowAdminAndRealtor(auth);
  }
  static canDeleteApartment(auth) {
    return this.allowAdminAndRealtor(auth);
  }

  static canUpdateOrDeleteApartment(authUser, apartment) {
    if (
      (authUser.role === UserRole.REALTOR &&
        authUser.uuid == apartment.userId) ||
      authUser.role === UserRole.ADMIN
    ) {
      return true;
    }
    return false;
  }

  static canSignupAdmin(auth) {
    return this.allowAdmin(auth);
  }
}
