import React from "react";
import Avatar from "@material-ui/core/Avatar";
import Button from "@material-ui/core/Button";
import CssBaseline from "@material-ui/core/CssBaseline";
import TextField from "@material-ui/core/TextField";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
import Grid from "@material-ui/core/Grid";
import Box from "@material-ui/core/Box";
import LockOutlinedIcon from "@material-ui/icons/LockOutlined";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import Container from "@material-ui/core/Container";
import SignUp from "../signup/signUp";
import { Link, withRouter } from "react-router-dom";
import UserService from "../../services/user.service";
import { useState } from "react";
import UserValidator from "../../libs/validators/user.validator";
import AuthUtil from "../../util/auth.util";
import { useAtom } from "jotai";
import { userAtom } from "../../atoms/atom";
import { useHistory } from "react-router-dom";
import { SuccessMessages } from "../../assets/constants/constants";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { CircularProgress } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(8),
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: "100%", // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));

function SignIn() {
  const classes = useStyles();
  const [email, setemail] = useState("");
  const [password, setpassword] = useState("");
  const [Error, setError] = useState("");
  const [loading, setloading] = useState(false);
  const [user, setUser] = useAtom(userAtom);
  let history = useHistory();
  async function handleSignIn(event) {
    event.preventDefault();
    const { error, value } = UserValidator.validateUsername({
      email,
      password,
    });

    if (error) {
      setError(error.details[0].message);
    } else {
      setError("");
      setloading(true);
      const res = await UserService.login({
        email: value.email,
        password: value.password,
      });
      setloading(false);
      if (!res.success) {
        setError(res.message);
        return;
      } else {
        toast.success(SuccessMessages.SignInSuccess, {
          position: "top-right",
          autoClose: 3000,
          closeOnClick: true,
        });
      }

      AuthUtil.setJWTToken(res.data.jwtToken);
      setUser(res.data.user);
      history.push("/");
    }
  }

  return (
    <Container component="main" maxWidth="xs">
      <CssBaseline />
      <div className={classes.paper}>
        <Avatar className={classes.avatar}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography component="h1" variant="h5">
          Sign in
        </Typography>
        <form className={classes.form} noValidate>
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            id="email"
            label="Email Address"
            name="email"
            autoComplete="email"
            autoFocus
            onChange={(event) => setemail(event.target.value)}
          />
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            name="password"
            label="Password"
            type="password"
            id="password"
            autoComplete="current-password"
            onChange={(event) => setpassword(event.target.value)}
          />
          <FormControlLabel
            control={<Checkbox value="remember" />}
            label="Remember me"
          />
          <Box color="error.main">{Error}</Box>
          <div style={{ display: "flex", justifyContent: "center" }}>
            {loading ? <CircularProgress /> : ""}
          </div>
          <Button
            type="submit"
            fullWidth
            variant="contained"
            color="primary"
            className={classes.submit}
            onClick={(e) => handleSignIn(e)}
          >
            Sign In
          </Button>
          <Grid container>
            <Grid item xs></Grid>
            <Grid item>
              <Link to="/signup" variant="body2">
                {"Don't have an account? Sign Up"}
              </Link>
            </Grid>
          </Grid>
          <Grid container>
            <Grid item xs></Grid>
            <Grid item></Grid>
          </Grid>
        </form>
      </div>
      <Box mt={8}></Box>
    </Container>
  );
}
export default withRouter(SignIn);
