import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Modal from "@material-ui/core/Modal";
import Backdrop from "@material-ui/core/Backdrop";
import Fade from "@material-ui/core/Fade";
import AddIcon from "@material-ui/icons/Add";
import Button from "@material-ui/core/Button";
import { RadioGroup, Radio, CircularProgress } from "@material-ui/core";
import CssBaseline from "@material-ui/core/CssBaseline";
import TextField from "@material-ui/core/TextField";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
import Link from "@material-ui/core/Link";
import Grid from "@material-ui/core/Grid";
import Box from "@material-ui/core/Box";
import LockOutlinedIcon from "@material-ui/icons/LockOutlined";
import Typography from "@material-ui/core/Typography";
import UpdateIcon from "@material-ui/icons/Update";
import TextareaAutosize from "@material-ui/core/TextareaAutosize";
import Container from "@material-ui/core/Container";
import { useEffect, useState } from "react";
import {
  filterApartmentsOptions,
  SuccessMessages,
  ApartmentAction,
} from "../../assets/constants/constants";
import { apartmentModalStyles } from "../styles/styles";
import ApartmentService from "../../services/apartment.service";
import ApartmentValidator from "../../libs/validators/apartment.validator";
import { ToastContainer, toast } from "react-toastify";
import { useAtom, atom } from "jotai";
import { apartmentAtom } from "../../atoms/atom";
export default function ApartmentModal(props) {
  const classes = apartmentModalStyles();
  const [apartments, setApartments] = useAtom(apartmentAtom);
  const [name, setname] = useState("");
  const [description, setdescription] = useState("");
  const [price, setprice] = useState(0);
  const [rooms, setrooms] = useState(0);
  const [floorSize, setfloorSize] = useState(0);
  const [lat, setlat] = useState("");
  const [long, setlong] = useState("");
  const [error, setError] = useState("");
  const [available, setavailable] = useState(false);
  const [loading, setloading] = useState(false);
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
    setError("");
    setavailable(false);
  };

  const updateApartment = async (e, apartmentId) => {
    e.preventDefault();

    let data = Object.assign(
      {},
      name == "" ? null : { name },
      description == "" ? null : { description },
      floorSize == 0 ? null : { floorSize },
      price == 0 ? null : { price },
      rooms == 0 ? null : { rooms },
      lat == "" ? null : { latitude: lat },
      long == "" ? null : { longitude: long },
      { available }
    );

    const { error, value } = ApartmentValidator.validateUpdateApartment(data);

    if (error) {
      setError(error.details[0].message);
    } else {
      setError("");
      setloading(true);
      const res = await ApartmentService.updateApartment(apartmentId, data);
      setloading(false);
      if (!res.success) {
        return;
      }
      const filteredApartments = apartments.map((apartment) =>
        apartment.uuid === apartmentId ? res.data : apartment
      );
      setApartments(filteredApartments);
      toast.success(SuccessMessages.ApartMentUpdatedSuccess, {
        position: "top-right",
        autoClose: 3000,
        closeOnClick: true,
      });
      handleClose();
    }
  };
  const createOrUpdateApartments = async (e) => {
    e.preventDefault();

    const data = {
      name,
      description,
      floorSize,
      price,
      rooms,
      latitude: lat,
      longitude: long,
    };
    const { error, value } = ApartmentValidator.validateCreateApartment(data);

    if (error) {
      setError(error.details[0].message);
    } else {
      setError("");
      setloading(true);
      const res = await ApartmentService.addApartment(data);
      setloading(false);
      if (!res.success) {
        return;
      }
      setApartments([...apartments, res.data]);
      toast.success(SuccessMessages.ApartMentCreatedSuccess, {
        position: "top-right",
        autoClose: 3000,
        closeOnClick: true,
      });
      handleClose();
    }
  };
  return (
    <div>
      {props.action == ApartmentAction.CreateApartment ? (
        <Button variant="contained" color="secondary" onClick={handleOpen}>
          <AddIcon className={classes.icon} fontSize="large" />
          Create New Apartment
        </Button>
      ) : (
        <UpdateIcon
          className={classes.icon}
          fontSize="large"
          color="primary"
          cursor="pointer"
          onClick={handleOpen}
        />
      )}
      <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        className={classes.modal}
        open={open}
        onClose={handleClose}
        closeAfterTransition
        BackdropProps={{
          timeout: 500,
        }}
      >
        <Fade in={open}>
          <div className={classes.paper}>
            <Container component="main" maxWidth="xs">
              <CssBaseline />
              <div className={classes.paper}>
                <Typography component="h1" variant="h5">
                  {props.action == ApartmentAction.CreateApartment
                    ? "Create Apartment"
                    : "Update Apartment"}
                </Typography>
                <form className={classes.form} noValidate>
                  <Grid container spacing={2}>
                    <Grid item xs={12}>
                      <TextField
                        autoComplete="name"
                        variant="outlined"
                        required
                        fullWidth
                        label="Name"
                        multiline="true"
                        autoFocus
                        onChange={(event) => setname(event.target.value)}
                      />
                    </Grid>
                    <Grid item xs={12}>
                      <TextField
                        autoComplete="description"
                        variant="outlined"
                        required
                        fullWidth
                        label="Description"
                        multiline="true"
                        autoFocus
                        onChange={(event) => setdescription(event.target.value)}
                      />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                      <TextField
                        autoComplete="rooms"
                        variant="outlined"
                        required
                        fullWidth
                        label="Rooms"
                        autoFocus
                        onChange={(event) => setrooms(event.target.value)}
                      />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                      <TextField
                        variant="outlined"
                        required
                        fullWidth
                        label="Price"
                        autoComplete="Price"
                        onChange={(event) => setprice(event.target.value)}
                      />
                    </Grid>
                    <Grid item xs={12}>
                      <TextField
                        variant="outlined"
                        required
                        fullWidth
                        label="Floor Size"
                        autoComplete="Floor Size"
                        onChange={(event) => setfloorSize(event.target.value)}
                      />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                      <TextField
                        autoComplete="longitude"
                        name="firstName"
                        variant="outlined"
                        required
                        fullWidth
                        id="firstName"
                        label="Longitude"
                        autoFocus
                        onChange={(event) => setlong(event.target.value)}
                      />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                      <TextField
                        variant="outlined"
                        required
                        fullWidth
                        label="Latitude"
                        autoComplete="latitude"
                        onChange={(event) => setlat(event.target.value)}
                      />
                    </Grid>

                    <Grid item xs={12} sm={6}>
                      {props.action == ApartmentAction.UpdateApartment ? (
                        <FormControlLabel
                          control={<Checkbox color="primary" />}
                          label="Available"
                          value={available}
                          onChange={(event) => {
                            setavailable(event.target.checked);
                          }}
                        />
                      ) : (
                        ""
                      )}
                    </Grid>
                  </Grid>
                  <Box color="error.main">{error}</Box>
                  <div style={{ display: "flex", justifyContent: "center" }}>
                    {loading ? <CircularProgress /> : ""} ;
                  </div>
                  <Button
                    type="submit"
                    fullWidth
                    variant="contained"
                    color="primary"
                    className={classes.submit}
                    onClick={(event) =>
                      props.action === ApartmentAction.CreateApartment
                        ? createOrUpdateApartments(event)
                        : updateApartment(event, props.apartmentId)
                    }
                  >
                    {props.action == ApartmentAction.CreateApartment
                      ? "Create Apartment"
                      : "Update Apartment"}
                  </Button>
                </form>
              </div>
              <Box mt={5}></Box>
            </Container>
          </div>
        </Fade>
      </Modal>
    </div>
  );
}
