import { Map, GoogleApiWrapper, Marker } from "google-maps-react";
import { useAtom } from "jotai";
import { Component, useEffect, useState } from "react";
import { apartmentAtom } from "../../atoms/atom";
import ApartmentService from "../../services/apartment.service";
require("dotenv").config();

const mapStyles = {
  width: "70%",
  height: "90%",
};

const displayMarkers = (apartments) => {
  return apartments.map((apartment, index) => {
    return (
      <Marker
        key={apartment.uuid}
        id={index}
        position={{
          lat: apartment.latitude,
          lng: apartment.longitude,
        }}
        onClick={() => console.log("You clicked me!")}
      />
    );
  });
};

const GoogleMap = (props) => {
  const [apartments, setApartments] = useAtom(apartmentAtom);

  useEffect(async () => {
    getApartments();
  }, []);
  const getApartments = async () => {
    const res = await ApartmentService.getApartments();
    if (!res.success) {
      //set toasts msg "unable to find apartmentss"
      return;
    }
    setApartments(res.data);
  };
  return (
    <div style={{ width: "100%", height: "550px" }}>
      <Map
        google={props.google}
        zoom={20}
        style={mapStyles}
        initialCenter={{ lat: 28.56197, lng: 77.16463 }}
      >
        {displayMarkers(props.apartments)}
      </Map>
      {props.apartments.map((apartment) => {
        <Marker
          key={apartment.uuid}
          id={apartment.uuid}
          position={{
            lat: apartment.latitude,
            lng: apartment.longitude,
          }}
          onClick={() => console.log("You clicked me!")}
        />;
      })}
    </div>
  );
};

export default GoogleApiWrapper({
  apiKey: process.env.googleMapApiKey,
})(GoogleMap);
