import * as Joi from "joi-browser";
import { GenConst } from "../assets/constants/constants";

export const apartmentCreateJoiSchema = Joi.object({
  name: Joi.string().min(3).required(),

  description: Joi.string().min(6).required(),

  rooms: Joi.number().min(1).max(50).required(),
  price: Joi.number().min(100).max(10000).required(),

  floorSize: Joi.number().min(100).max(100000).required(),

  latitude: Joi.number().min(-90).max(90).required(),

  longitude: Joi.number().min(-180).max(180),
});
export const apartmentUpdateJoiSchema = Joi.object({
  name: Joi.string().min(3),

  description: Joi.string().min(6),
  rooms: Joi.number().min(1).max(50),
  price: Joi.number().min(100).max(10000),

  floorSize: Joi.number().min(100).max(100000),

  latitude: Joi.number().min(-90).max(90),

  longitude: Joi.number().min(-180).max(180),

  available: Joi.boolean(),
});

export const apartmentFilterJoiSchema = Joi.object({
  min_size: Joi.number().min(GenConst.min_size).max(GenConst.max_size),

  max_size: Joi.number().min(GenConst.min_size).max(GenConst.max_size),

  min_price: Joi.number().min(GenConst.min_price).max(GenConst.max_price),

  max_price: Joi.number().min(GenConst.min_price).max(GenConst.max_price),

  min_rooms: Joi.number().min(GenConst.min_rooms).max(GenConst.max_rooms),

  max_rooms: Joi.number().min(GenConst.min_rooms).max(GenConst.max_rooms),
});
