import * as Joi from "joi-browser";
import {
  apartmentCreateJoiSchema,
  apartmentFilterJoiSchema,
  apartmentUpdateJoiSchema,
} from "../../joi-schema/apartment-joi-schema";
class ApartmentValidator {
  static validateCreateApartment(data) {
    const res = Joi.validate(data, apartmentCreateJoiSchema);
    return res;
  }
  static validateUpdateApartment(data) {
    const res = Joi.validate(data, apartmentUpdateJoiSchema);
    return res;
  }

  static validateFilterApartment(data) {
    const res = Joi.validate(data, apartmentFilterJoiSchema);
    return res;
  }
}

export default ApartmentValidator;
