import { useAtom } from "jotai";
import React, { Fragment, useEffect, useState } from "react";
import { Redirect, Route, useHistory, withRouter } from "react-router";
import App from "../../App";
import { RoutesUrl, UserRole } from "../../assets/constants/constants";
import { userAtom } from "../../atoms/atom";
import UserService from "../../services/user.service";
import AuthUtil from "../../util/auth.util";
import User from "../users/users";
import SignIn from "../signin/signIn";
import SignUp from "../signup/signUp";

function ProtectedRoutes({ Component, path, isAuth, ...props }) {
  const [user, setUser] = useAtom(userAtom);
  const [loader, setLoader] = useState(true);
  let history = useHistory();

  useEffect(() => {
    const getUser = async () => {
      if (!user.username) {
        if (!AuthUtil.getJWTToken()) {
          setUser({});
        } else {
          const res = await UserService.getCurrentUser();

          if (!res.success) setUser({});
          else setUser(res.data.authUser);
        }
      }

      setLoader(false);
    };
    getUser();
  }, []);

  if (loader) {
    return <div></div>;
  }

  if (user && user.username) {
    if (props.location.pathname === "/") {
      return <Route render={(props) => <App {...props} />} />;
    } else if (
      props.location.pathname == "/users" &&
      user.role === UserRole.ADMIN
    ) {
      return <Route render={(props) => <User />} />;
    } else {
      return (
        <Redirect
          to={{
            pathname: "/",
            state: { from: props.location },
          }}
        />
      );
    }
  }
  if (!user.username) {
    if (
      props.location.pathname == "/signin" ||
      props.location.pathname === "/signup"
    ) {
      return <Route render={(props) => <Component {...props} />} />;
    } else {
      return (
        <Redirect
          to={{
            pathname: "/signin",
            state: { from: props.location },
          }}
        />
      );
    }
  }
}

export default withRouter(ProtectedRoutes);
