import React from "react";
import ReactDOM from "react-dom";
import "./index.css";
import App from "./App";
import reportWebVitals from "./reportWebVitals";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Signin from "./components/signin/signIn";
import Signup from "./components/signup/signUp";
import Users from "./components/users/users";
import { Provider } from "jotai";
import ProtectedRoutes from "../src/components/protectedRoutes/protectedRoutes";
import Home from "./components/home/home";
import AuthUtil from "./util/auth.util";
import SignIn from "./components/signin/signIn";
import { UserRole } from "./assets/constants/constants";
ReactDOM.render(
  <React.Fragment>
    <Provider>
      <Router>
        <Switch>
          <ProtectedRoutes path="/signup" exact Component={Signup} />
          <ProtectedRoutes
            path="/users"
            exact
            component={Users}
            role={UserRole.ADMIN}
          />
          <ProtectedRoutes path="/signin" Component={SignIn} />
          <ProtectedRoutes path="/" Component={App} />
        </Switch>
      </Router>
    </Provider>
  </React.Fragment>,
  document.getElementById("root")
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
